img {
  max-width: 100%;
  height: auto;
}

a {
  color: #555;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

fieldset {
  height: auto;
  border: 0;
  margin: 0;
  padding: 0;
}

label {
  cursor: pointer;
}

input:focus {
  border: 2px solid #555;
}

iframe {
  min-width: 100%;
}

h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

[class*=__container] {
  max-width: 87.5rem;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  margin: 0 auto;
}

input:focus::placeholder {
  opacity: 0;
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

/* button */
/* menu */
@media (width < 769px) {
  .menu-nav__button {
    order: 1;
    margin: 0;
    padding: 0;
    display: block;
    cursor: pointer;
    border: none;
    background-color: transparent;
  }
}
@media (width > 767px) {
  .menu-nav__button {
    display: none;
  }
}

/* menu-icon */
@media (width < 769px) {
  .menu-nav__icon {
    stroke: #555;
  }
  .menu-nav__icon:hover {
    stroke: red;
  }
}
.menu-nav__icon-menu,
.menu__button--open .menu-nav__icon-cross {
  display: block;
}

.menu-nav__icon-cross,
.menu__button--open .menu-nav__icon-menu {
  display: none;
}

/* color */ /* fonts */
@font-face {
  font-family: Roboto;
  src: url("/fonts/roboto/Roboto-Regular.woff2") format("woff2"), url("/fonts/roboto/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Roboto;
  src: url("/fonts/roboto/Roboto-Medium.woff2") format("woff2"), url("/fonts/roboto/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
/* page */
.page {
  height: 100%;
  font-size: 20px;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  line-height: 20px;
}

.page__body {
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  color: #555;
}
@media (min-width: 768px) {
  .page__body {
    min-width: 768px;
  }
}

@media (min-width: 1280px) {
  .page-main {
    margin-inline: 41px 88px;
  }
}
/* header */
.header {
  margin-inline: auto;
  padding-inline: 3%;
  padding-block: 2.25rem;
  min-width: 320px;
  width: 100%;
  max-width: 1280px;
  position: relative;
  display: flex;
  column-gap: 50px;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(249, 255, 161);
}

.header__phone {
  margin-top: 4px;
  margin-right: auto;
  position: relative;
  font-weight: 600;
  font-size: 1.125rem;
  line-height: 125%;
}
.header__phone::before {
  content: url("");
  background-image: url("../../img/icons/phone.svg");
  background-size: 20px 21px;
  background-repeat: no-repeat;
  position: absolute;
  width: 21px;
  height: 21px;
  top: 0;
  left: -25px;
}

/* logo */
.logo {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 8px;
}

.logo__img {
  width: 40px;
  height: 40px;
}
@media (width < 768px) {
  .logo__img {
    grid-row: span 2;
  }
}

.logo__title {
  margin: 0;
}
@media (width >= 768px) {
  .logo__title {
    align-self: center;
  }
}

.logo__tagline {
  margin: 0;
}
@media (width > 767px) {
  .logo__tagline {
    display: none;
  }
}

/* menu */
.menu-nav {
  color: red;
  display: flex;
}

/* list */
.menu-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
  font-size: 12px;
  align-self: center;
}

/* item */
.menu-nav__item {
  text-transform: uppercase;
}

/* link */
.menu-site__link--active {
  padding-bottom: 3px;
  font-weight: bold;
  border-bottom: 2px solid grey;
}

/* footer */
.footer {
  margin-top: auto;
  margin-inline: auto;
  min-width: 320px;
  width: 100%;
  max-width: 1280px;
  min-height: 150px;
  background-color: rgb(161, 249, 255);
}

.footer__container {
  padding-top: 66px;
  padding-bottom: 52px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.footer__list {
  padding-inline: 3%;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.social__list {
  display: flex;
  justify-content: space-between;
}

.copyright {
  margin-top: auto;
  padding-block: 20px;
  text-align: center;
  background-color: #111E22;
}

.copyright span {
  color: #fff;
  font-weight: 400;
  font-size: 15px;
  line-height: 200%;
}